import Vue from 'vue'
import App from './App.vue'
import VModal from 'vue-js-modal'
import global from './global'

import './assets/tailwind.css'

Vue.config.productionTip = false;
Vue.use(VModal);
Vue.prototype.global = global; // 註冊為全域

new Vue({
    render: h => h(App),
}).$mount('#app')