<template>
  <div>
    <h4>{{ this.global.titles[name] }}</h4>
    <button class="btn w-full py-6 text-xl" @click="show(`counts-${name}`)">
      {{ numberFormat }} <small>{{ setunit }}</small>
    </button>
    <!-- light box -->
    <SetCount :name="name" :val="val" :modalId="`counts-${name}`" />
  </div>
</template>

<script>
import SetCount from './SetCount.vue'

export default {
  components: {
    SetCount
  },
  props: {
    name: {
      trpe: String,
      required: true
    },
    val: {
      trpe: String,
      required: true
    }
  },
  computed: {
    setunit() {
      const unit = {
        kg: 'KG',
        percent: '雙',
        company: '家',
        cleanBeach: '場',
        bottles: '個'
      };
      return unit[this.name];
    },
    numberFormat() {
      let n = parseInt(this.val);
      const decimal = 0; // 小數點N位
      const d = '.'; // 小數點符
      const t = ','; // 千位符
      const minus = n < 0 ? '-' : '';
      const i = String(parseInt(n = Math.abs(Number(n) || 0).toFixed(decimal)));
      let j = (j = i.length) > 3 ? j % 3 : 0;
      return `${minus}${(j ? i.substr(0, j) + t : '')}${i.substr(j).replace(/(\d{3})(?=\d)/g, '$1' + t)}${(decimal ? d + Math.abs(n - i).toFixed(decimal).slice(2) : '')}`;
    }
  },
  methods: {
    show(target) {
      this.$modal.show(target);
    }
  }
}
</script>
