<template>
  <tr class="hover:bg-gray-100">
    <th class="px-4 py-2 border border-gray-400">{{ list.myorder }}</th>
    <td class="px-4 py-2 border border-r-0 border-gray-400">
      <div class="flex items-center">
        <v-icon
          v-if="list.ismedia === '1'"
          class="text-red-500 mr-2"
          name="brands/youtube"
          scale="1.4"
        />
        <h5>{{ list.title }}</h5>
      </div>
    </td>
    <td class="text-right px-4 py-2 border border-l-0 w-px border-gray-400">
      <button class="btn" @click="show(`article-${list.id}`)">修改</button>
      <button class="btn" @click="deleteList">刪除</button>
      <!-- light box -->
      <SetArticle :list="list" :modalId="`article-${list.id}`" />
    </td>
  </tr>
</template>

<script>
import SetArticle from './SetArticle.vue'
import Icon from 'vue-awesome/components/Icon'
import 'vue-awesome/icons/brands/youtube'

import axios from 'axios'
import qs from 'qs'

export default {
  components: {
    SetArticle,
    'v-icon': Icon
  },
  props: {
    list: {
      type: Object,
      required: true
    }
  },
  methods: {
    show(target) {
      this.$modal.show(target);
    },
    deleteList() {
      if(confirm('確定刪除嗎?')) {
        axios({
          method: 'post',
          url: this.global.apiPath,
          data: qs.stringify({
            action: 'delete',
            id: this.list.id
          })
        }).then(response => {
          if(response.data.result === 'success') {
            alert('刪除成功');
            location.reload();
          } else {
            alert('刪除發生錯誤');
            console.log(response);
          }
        }).catch((error) => {
          alert('刪除失敗');
          console.error(error);
        })
      }
    },
  }
}
</script>
