<template>
  <div class="my-16 relative">
    <div class="flex justify-between items-center">
      <h2 class="text-4xl mb-4">{{ this.global.titles[list[0].category] }}</h2>
      <div class="px-4">
        <button class="btn" @click="show(`add-${list[0].category}`)">新增</button>
      </div>
    </div>
    <SetArticle :modalId="`add-${list[0].category}`" :category="list[0].category" />
    <div v-if="list[0].title">
      <table class="w-full border-collapse">
        <thead class="bg-gray-600">
          <tr>
            <th class="w-24 px-4 py-2 border border-gray-400 text-white">排序</th>
            <th colspan="2" class="text-left px-4 py-2 border border-gray-400 text-white">標題</th>
          </tr>
        </thead>
        <tbody
          v-for="(articlesArr, index) in allocationData"
          :key="index"
          :class="index === active ? 'table-row-group' : 'hidden'"
        >
          <ArticleList
            v-for="list in articlesArr"
            :key="list.index"
            :list="list"
          />
        </tbody>
      </table>
      <div class="float-right mt-2">共{{ list.length }}筆</div>
      <!-- 分頁 -->
      <div class="text-center mt-4" v-if="allocationData.length > 1">
        <ul class="inline-flex">
          <li
            v-for="(articlesArr, index) in allocationData"
            :key="index"
            :class="index === active ?
              'px-3 py-2 mx-1 text-white bg-gray-600 cursor-pointer' :
              'px-3 py-2 mx-1 text-white bg-gray-400 cursor-pointer hover:bg-gray-600'"
            @click="setActive(index)"
          >
            {{ index + 1 }}
          </li>
        </ul>
      </div>
    </div>
    <div v-else>
      <p class="py-8 text-center border border-gray-400">目前尚無資料</p>
    </div>
  </div>
</template>

<script>
import ArticleList from './ArticleList.vue'
import SetArticle from './SetArticle.vue'

export default {
  components: {
    ArticleList,
    SetArticle
  },
  props: {
    list: {
      trpe: Array,
      required: true
    }
  },
  data() {
    return {
      denominator: 8, // 每頁N筆
      allocationData: [], // 計算後為二維陣列
      active: 0
    }
  },
  methods: {
    show(target) {
      this.$modal.show(target);
    },
    setActive(active) {
      this.active = active;
    }
  },
  watch: {
    list: function(newdata) {
      this.allocationData = (() => {
        let newobj = [];
        const myList = Math.ceil(newdata.length / this.denominator);
        for(let i = 1; i <= myList; i++) {
          let arr = [];
          for(let list = this.denominator * (i - 1); list < this.denominator * i; list++) {
            if(newdata[list]) {
              arr.push(newdata[list]);
            } else {
              break;
            }
          }
          newobj.push(arr);
        }
        return newobj;
      })();
    }
  }
}
</script>
