<template>
  <modal
    :name="modalId"
    width="60%"
    height="45%"
    @closed="closed"
  >
    <div class="flex items-center absolute w-full h-full px-8 py-4 text-left">
      <div class="w-full">
        <h3 class="w-1/2 mx-auto text-3xl mb-2">{{ this.global.titles[name] }}</h3>
        <table class="w-1/2 mx-auto border-collapse bg-gray-100">
          <tbody>
            <tr>
              <td class="px-4 py-2 border border-gray-400">
                <div class="my-4">
                  <input type="number" class="input-text block w-1/2 mx-auto" min="0" v-model="myval">
                </div>
              </td>
            </tr>
          </tbody>
        </table>
        <button class="btn mx-auto block my-6" @click="submit">更新數據</button>
      </div>
    </div>
  </modal>
</template>

<script>
import axios from 'axios'
import qs from 'qs'

export default {
  props: {
    modalId: {
      type: String,
      required: true
    },
    name: {
      trpe: String,
      required: true
    },
    val: {
      trpe: String,
      required: true
    }
  },
  data() {
    return {
      myval: this.val
    }
  },
  methods: {
    closed() {
      this.myval = this.val;
    },
    submit() {
      if(!this.myval) {
        alert('尚有欄位未填寫');
        return false;
      }
      if(this.myval < 0) {
        alert('數據需大於0');
        return false;
      }
      if(!confirm('確定送出嗎?')) return false;
      axios({
        method: 'post',
        url: this.global.apiPath,
        data: qs.stringify({
          action: 'setCounts',
          category: this.name,
          count: this.myval
        })
      }).then(response => {
        if(response.data.result === 'success') {
          alert(`更新成功`);
          location.reload();
        } else {
          alert(`更新發生錯誤`);
          console.log(response);
        }
      }).catch((error) => {
        alert(`更新失敗`);
        console.error(error);
      })
    }
  }
}
</script>
