<template>
  <modal
    :name="modalId"
    width="60%"
    height="45%"
    @closed="closed"
  >
    <div class="flex items-center absolute w-full h-full px-8 py-4 text-left">
      <div class="w-full">
        <h3 class="text-3xl mb-2">{{ this.global.titles[category || list.category] }}</h3>
        <table class="w-full border-collapse bg-gray-100">
          <tbody>
            <tr>
              <td class="px-4 py-2 border border-gray-400 w-32">
                <h4>排序</h4>
                <input type="number" class="input-text" v-model="myorder">
              </td>
              <td class="px-4 py-2 border border-gray-400">
                <div class="my-4">
                  <h4 class="inline-block">類別</h4>
                  <label class="p-2 ml-4">
                    <input class="mr-1 text-lg transform scale-125" type="radio" v-model="ismedia" value="1">影片
                  </label>
                  <label class="p-2">
                    <input class="mr-1 text-lg transform scale-125" type="radio" v-model="ismedia" value="0">文章
                  </label>
                </div>
                <hr class="border-gray-400">
                <div class="my-4">
                  <h4>標題</h4>
                  <input type="text" class="input-text" v-model="title">
                </div>
                <div class="my-4">
                  <h4>引言</h4>
                  <input type="text" class="input-text" v-model="subtitle">
                </div>
                <div class="my-4">
                  <h4>文章連結</h4>
                  <input type="text" class="input-text" v-model="url">
                </div>
                <div class="my-4">
                  <h4>{{ ismedia === '1' ? 'Youtube影片編號' : '圖片' }}</h4>
                  <input type="text" class="input-text" v-model="image">
                </div>
              </td>
            </tr>
          </tbody>
        </table>
        <button class="btn mx-auto block my-6" @click="submit">確認送出</button>
      </div>
    </div>
  </modal>
</template>

<script>
import axios from 'axios'
import qs from 'qs'

export default {
  props: {
    list: {
      type: Object,
      default: () => {
        return {
          id: '',
          ismedia: '0',
          title: '',
          subtitle: '',
          url: '',
          image: '',
          myorder: '0'
        }
      }
    },
    category: {
      type: String,
      default: ''
    },
    modalId: {
      type: String,
      required: true
    }
  },
  data() {
    // 取props.list數值當作data
    const {ismedia, myorder, title, subtitle, url, image} = this.list;
    return {ismedia, myorder, title, subtitle, url, image};
  },
  methods: {
    closed() {
      this.ismedia = this.list.ismedia;
      this.myorder = this.list.myorder;
      this.title = this.list.title;
      this.subtitle = this.list.subtitle;
      this.url = this.list.url;
      this.image = this.list.image;
    },
    submit() {
      if(!this.myorder || !this.title || !this.subtitle || !this.url || !this.image) {
        alert('尚有欄位未填寫');
        return false;
      }
      if(!confirm('確定送出嗎?')) return false;
      const actionTpye = this.list.id ? 'update' : 'add';
      axios({
        method: 'post',
        url: this.global.apiPath,
        data: qs.stringify({
          action: actionTpye,
          id: this.list.id,
          ismedia: this.ismedia,
          myorder: this.myorder,
          title: this.title,
          subtitle: this.subtitle,
          url: this.url,
          image: this.image,
          category: this.category
        })
      }).then(response => {
        if(response.data.result === 'success') {
          alert(`${this.global.titles[actionTpye]}成功`);
          location.reload();
        } else {
          alert(`${this.global.titles[actionTpye]}發生錯誤`);
          console.log(response);
        }
      }).catch((error) => {
        alert(`${this.global.titles[actionTpye]}失敗`);
        console.error(error);
      })
    }
  }
}
</script>
