<template>
  <div id="app" class="max-w-screen-lg mx-auto my-10">
    <Counts v-if="data" :data="data.counts" />

    <!-- news -->
    <ArticleArea v-if="data && data.articles.news" :list="data.articles.news" />
    <ArticleArea v-else :list="[{category: 'news'}]" />

    <!-- sliders -->
    <ArticleArea v-if="data && data.articles.sliders" :list="data.articles.sliders" />
    <ArticleArea v-else :list="[{category: 'sliders'}]" />
  </div>
</template>

<script>
import Counts from './components/counts/Counts.vue'
import ArticleArea from './components/articles/ArticleArea.vue'

import axios from 'axios'
import qs from 'qs'

export default {
  name: 'App',
  components: {
    Counts,
    ArticleArea
  },
  data() {
    return {
      data: null
    }
  },
  mounted() {
    const httpMethod = process.env.NODE_ENV === 'production' ? 'post' : 'get';
    axios({
      method: httpMethod,
      url: this.global.apiPath,
      data: qs.stringify({
        action: 'getAdmin'
      })
    }).then(response => {
      if(Object.keys(response.data).length > 0) {
        this.data = response.data;
      } else {
        alert('取得資料錯誤');
        console.log(response);
      }
    }).catch((error) => {
      alert('取得資料失敗');
      console.error(error);
    })
  }
}
</script>

